import React from "react";
import AOS from 'aos';
import 'aos/dist/aos.css';
import yourImage from "../../assets/header/yourImage.gif"; // Adjust the path based on your project structure

// Initiate Animations
AOS.init({ offset: 120, delay: 0, duration: 700, easing: 'ease' });
window.addEventListener('load', AOS.refresh);

// Heading Component
const Heading = () => (
  <div
    id='heading'
    className='flex flex-col items-center px-5p relative z-10'
    data-aos="fade"
    data-aos-delay="150"
    data-aos-once='true'
  >
    {/* Description & Title Image */}
    <h3 className='font-VT323 text-2xl z-20'>4200 ðŸ¦† are set to fly on $SEI.!</h3>
    <img src={yourImage} alt="Your Image" className="w-1/5 max-w-screen-md" />


    <h3 className='font-VT323 text-4xl z-20 text-white bg-orange p-4 shadow-md'>MEMPOOL DUCKS</h3>

    {/* Updated Launch Button to redirect to a placeholder link */}
    <a
      className='relative z-10'
      href="https://mempoolducks.github.io/MINT/"
      target="_blank"
      rel="noopener noreferrer"
    >
      <div className='launch-button-outer absolute'></div>
      <div className='launch-button-inner absolute w-60 h-10 font-pressstart text-sm filter hover:brightness-95'>
        MINT SOON
      </div>
    </a>

    {/* SignUp Input */}
    <div className='text-sm inline-flex justify-center flex-wrap mt-40 mb-72 font-pressstart z-10'>
      <a
        href='https://discord.gg/vG5Eufnp'
        target='_blank'
        rel='noopener noreferrer'
        className='bg-purple inline-flex items-center justify-center cursor-pointer h-10 w-72 transition duration-300 ease-in-out filter hover:brightness-95 text-white text-decoration-none'
      >
        JOIN OUR DISCORD SERVER
      </a>
    </div>
  </div>
);

export default Heading;
